@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
}

.App-Name {
  font-family: 'Patrick Hand SC', cursive;
}

.ButtonBar {
  width: 100%;
  
  display: inline-flex;

  justify-content: space-evenly;
}

.left {
  float: left;
}

.logo {
  height: 100px;
}

.right {
  float: right;
}

.small-font {
  font-size: small;
}

img {
  height: 100%;
}

.SecretSanta {
    height: 90vh;
    
    display: flex;

    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Start {
    height: 100px;
    width: 50vw;
    display: flex;

    justify-content: center;
}

.Start > div {
    display: flex;
    flex-direction: column;
    
    justify-content: center;
    align-items: center;
}
.SantaInput {
    display: flex;

    justify-content: center;
}

.SantaInput > input {
    width: 200px;
}
.SantaCard {
    width: 150px;

    margin: 6px;
}

.SantaTable {
    margin: 6px;
    overflow-y: scroll;
    max-height: 40vh;
}
.Overlay-Result > * {
    text-align: left;
}
.Overlay {
    position: fixed;
    width: 100%;
    height: 100%;

    top: 0;
    left: 0;
}

.Overlay-Container {
    position: absolute;
    background: white;

    width: 100%;
    height: 300px;

    z-index: 10;
    
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    border-width: 1px;
    border-color: black;
    border-style: solid;
    border-radius: 10px;
}

.Overlay-Container > * {
    margin: 10px;
}

.Overlay-Container > .card-body {
    overflow: scroll;
}

.Overlay-Greyscreen {
    position: absolute;
    width: 100%;
    height: 100%;

    top: 0;

    opacity: 0.5;
    background: grey;
}

@media only screen and (min-width: 425px) {
    .Overlay-Container {
        width: 400px;
        height: 350px;
    }
}

@media only screen and (min-width: 1024px) {
    .Overlay-Container {
        width: 500px;
        height: 400px;
    }
}


.Header {
    justify-content: center;
}

.Header > div {
    height: 100%;
    align-items: center;
}

.Footer-links > button {
    color: black;
}

.Footer-links > button:hover {
    color: #555;
}

.Changelog {
    text-align: left;
}

@media only screen and (max-width: 600px) {
    .Footer-links > button {
        font-size: smaller;
        padding-left: 6px;
        padding-right: 6px;
    }
  }
